<template>
  <el-row>
    <el-col :span="24">
      <el-button class="marginBottom" size="mini" @click="returnList"></el-button>
      <div class="platformContainer">
        <span></span>
        <el-image class="floatRight marginBottom" v-if="EquipmentData.QRCode" style="width: 50px;height: 50px;" :src="'data:;base64,'+EquipmentData.QRCode" :preview-src-list="('data:;base64,'+EquipmentData.QRCode).split() "></el-image>
        <table class="elementTable text-size-14">
          <tr>
            <td></td>
            <td>{{ EquipmentData.EquipmentCode }}</td>
            <td></td>
            <td>{{ EquipmentData.EquipmentName }}</td>
            <td></td>
            <td>{{ EquipmentData.AssetCode }}</td>
            <td></td>
            <td>{{ EquipmentData.LeaveFactoryCode }}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{ EquipmentData.EquipmentType }}</td>
            <td></td>
            <td>{{ EquipmentData.Brand }}</td>
            <td></td>
            <td>{{ EquipmentData.Specs }}</td>
            <td></td>
            <td>{{ EquipmentData.Unit }}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{ EquipmentData.Source }}</td>
            <td></td>
            <td>{{ EquipmentData.Manufacturer }}</td>
            <td></td>
            <td>{{ EquipmentData.WarrantyTime }}</td>
            <td></td>
            <td>{{ EquipmentData.NetValue }}</td>
          </tr>
            <td></td>
            <td colspan="3">
              <span class="btn-block bg-success color-white" v-if="EquipmentData.Status === ''">{{ EquipmentData.Status }}</span>
              <span class="btn-block bg-orange color-white" v-if="EquipmentData.Status === ''">{{ EquipmentData.Status }}</span>
              <span class="btn-block bg-darkblue color-white" v-if="EquipmentData.Status === ''">{{ EquipmentData.Status }}</span>
              <span class="btn-block bg-red color-white" v-if="EquipmentData.Status === ''">{{ EquipmentData.Status }}</span>
              <span class="btn-block bg-grayblack color-white" v-if="EquipmentData.Status === ''">{{ EquipmentData.Status }}</span>
            </td>
            <td></td>
            <td colspan="3">
              <span class="btn-block color-white" style="background: #FF5722;" v-if="EquipmentData.Grade === 'A'">A()</span>
              <span class="btn-block color-white" style="background: #813EDB;" v-if="EquipmentData.Grade === 'B'">B()</span>
              <span class="btn-block color-white" style="background: #5FB878;"v-if="EquipmentData.Grade === 'C'">C()</span>
            </td>
          <tr>
            <td></td>
            <td>{{ EquipmentData.PurchaseTime }}</td>
            <td></td>
            <td>{{ EquipmentData.PurchaseMoney }}</td>
            <td></td>
            <td>{{ EquipmentData.UseTime }}</td>
            <td></td>
            <td>{{ EquipmentData.ScrapTime }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="7">{{ EquipmentData.Director }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="7">{{ EquipmentData.Department }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="7">{{ EquipmentData.Position }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="3">{{ EquipmentData.IsDepreciation }}</td>
            <td></td>
            <td colspan="3">{{ EquipmentData.IsCalculate }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="7">{{ EquipmentData.TechnicalParameter }}</td>
          </tr>
          <tr>
            <td></td>
            <td colspan="7">{{ EquipmentData.Comment }}</td>
          </tr>
        </table>
      </div>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: "EquipmentDetails",
    data(){
      return{
        EquipmentData:{}
      }
    },
    mounted(){
      this.getTableData()
    },
    methods:{
      getTableData(){
        var that = this
        var params = {
          tableName: "Equipment",
          EquipmentCode:this.$store.state.EquipmentCode,
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res => {
          if(res.data.code === "200"){
            this.EquipmentData = res.data.data.rows[0]
          }else{
            that.$message({
              type: 'info',
              message: res.data.message
            });
          }
        })
      },
      returnList(){
        this.$router.push("/list")
      }
    }
  }
</script>

<style scoped>

</style>



