<template>
  <el-row>
    <el-col :span="24">
      <el-col :span="24">
        <div class="platformContainer">
          <tableView class="" :tableData="TableData" @getTableData="getTableData" @privileges="privileges" @teamGroup="teamGroup"></tableView>
        </div>
        <el-dialog :title="selectPersonnelName" :visible.sync="dialogVisible" width="50%" :append-to-body="true">
          <el-transfer :titles="['', '']" :button-texts="['', '']" v-model="transferValue" :data="transferData"></el-transfer>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="savePrivileges"></el-button>
          </span>
        </el-dialog>
        <el-dialog :title="selectPersonnelName" :visible.sync="teamGroupDialogVisible" width="50%">
          <el-transfer :titles="['', '']" :button-texts="['', '']" v-model="transferTeamGroupValue" :data="transferTeamGroupData"></el-transfer>
          <span slot="footer" class="dialog-footer">
            <el-button @click="teamGroupDialogVisible = false"> </el-button>
            <el-button type="primary" @click="saveTeamGroup"></el-button>
          </span>
        </el-dialog>
      </el-col>
    </el-col>
  </el-row>
</template>

<script>
  var moment = require('moment');
  import tableView from '@/components/CommonTable'
  export default {
    name: "Personnel",
    components:{tableView},
    data(){
      return {
        TableData:{
          tableName:"User",
          column:[
            {label:"ID",prop:"ID",type:"input",value:"",disabled:true,showField:false,searchProp:false},
            {prop:"Name",label:"",type:"input",value:""},
            {prop:"WorkNumber",label:"",type:"input",value:""},
            {prop:"Password",label:"",type:"input",value:"",showField:false,searchProp:false},
            {prop:"Creater",label:"",type:"input",value:"",searchProp:false,canSubmit:false},
            {prop:"LastLoginTime",label:"",type:"input",value:"",searchProp:false,canSubmit:false},
          ],
          data:[],
          limit:5,
          offset:1,
          total:0,
          searchProp:"",
          tableSelection:true, //
          tableSelectionRadio:false, //
          searchVal:"",
          multipleSelection: [],
          dialogVisible: false,
          dialogTitle:'',
          handleType:[
            {type:"primary",label:"",hasPermissions:['']},
            {type:"warning",label:"",hasPermissions:['']},
            {type:"danger",label:"",hasPermissions:['']},
            {type:"primary",label:"",clickEvent:"privileges",hasPermissions:['']},
            {type:"primary",label:"",clickEvent:"teamGroup",hasPermissions:['']},
          ],
        },
        selectPersonnelName:"",
        dialogVisible:false,
        transferValue:[],
        transferData:[],
        teamGroupDialogVisible:false,
        transferTeamGroupValue:[],
        transferTeamGroupData:[],
      }
    },
    created(){

    },
    mounted() {
      this.getTableData()
    },
    methods:{
      getTableData(){
        var that = this
        var params = {
          tableName: this.TableData.tableName,
          limit:this.TableData.limit,
          offset:this.TableData.offset - 1
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res =>{
          if(res.data.code === "200"){
            var data = res.data.data
            that.TableData.data = data.rows
            that.TableData.total = data.total
          }
        },res =>{
          console.log("")
        })
      },
      privileges(){
        if(this.TableData.multipleSelection.length === 1){
          this.dialogVisible = true
          this.selectPersonnelName = ' '+this.TableData.multipleSelection[0].Name+' '
          this.transferData = []
          this.transferValue = []
          var that = this
          var params = {
            UserID:this.TableData.multipleSelection[0].ID
          }
          this.axios.get("/api/role_management/selectrolebyuser",{
            params: params
          }).then(res =>{
            res.data.notHaveRows.forEach(item =>{
              that.transferData.push({
                key:item.ID,
                label:item.RoleName
              })
            })
            res.data.existingRows.forEach(item =>{
              that.transferValue.push(item.ID)
            })
          },res =>{
            console.log("")
          })
        }else{
          this.$message({
            type: 'info',
            message: ''
          });
        }
      },
      savePrivileges(){
        var selectPermissionArr = []
        this.transferValue.forEach(item =>{
          selectPermissionArr.push(item)
        })
        var params = {
          UserID: this.TableData.multipleSelection[0].ID,
          RoleIDs:JSON.stringify(selectPermissionArr)
        }
        this.axios.post("/api/role_management/saveroleuser",this.qs.stringify(params)).then(res =>{
          if(res.data.code === "200"){
            this.$message({
              type: 'success',
              message: ''
            });
            this.dialogVisible = false
          }
        },res =>{
          console.log("")
        })
      },
      teamGroup(){
        if(this.TableData.multipleSelection.length === 1){
          this.teamGroupDialogVisible = true
          this.selectPersonnelName = ' '+this.TableData.multipleSelection[0].Name+' '
          this.transferTeamGroupData = []
          this.transferTeamGroupValue = []
          var that = this
          var params = {
            userID:this.TableData.multipleSelection[0].ID
          }
          this.axios.get("/api/selectUserShiftsGroup",{
            params: params
          }).then(res =>{
            res.data.notHaveRows.forEach(item =>{
              that.transferTeamGroupData.push({
                key:item.ID,
                label:item.ShiftsGroupName
              })
            })
            res.data.existingRows.forEach(item =>{
              that.transferTeamGroupValue.push(item.ID)
            })
          },res =>{
            console.log("")
          })
        }else{
          this.$message({
            type: 'info',
            message: ''
          });
        }
      },
      saveTeamGroup(){
        var selectPermissionArr = []
        this.transferTeamGroupValue.forEach(item =>{
          selectPermissionArr.push(item)
        })
        var params = {
          userID: this.TableData.multipleSelection[0].ID,
          shiftsgroupIDs:JSON.stringify(selectPermissionArr)
        }
        this.axios.post("/api/saveuserusershiftsgroup",this.qs.stringify(params)).then(res =>{
          if(res.data.code === "200"){
            this.$message({
              type: 'success',
              message: ''
            });
            this.teamGroupDialogVisible = false
          }
        },res =>{
          console.log("")
        })
      }
    }
  }
</script>

<style scoped>

</style>



