<template>
  <el-row>
    <el-col :span="24">
      <mt-header title=''></mt-header>
      <mt-cell title="" :value="EquipmentData.EquipmentCode"></mt-cell>
      <mt-cell title="" :value="EquipmentData.EquipmentName"></mt-cell>
      <mt-cell title="" :value="EquipmentData.LeaveFactoryCode"></mt-cell>
      <mt-cell title="" :value="EquipmentData.Specs"></mt-cell>
      <mt-cell title="" :value="EquipmentData.Brand"></mt-cell>
      <mt-cell title="" :value="EquipmentData.EquipmentType"></mt-cell>
      <mt-cell title="" :value="EquipmentData.Manufacturer"></mt-cell>
      <mt-cell title="" :value="EquipmentData.Position"></mt-cell>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: "mobileEquipmentDetails",
    data(){
      return {
        EquipmentData:{},
      }
    },
    created(){
      document.title = ""
      this.getTableData()
    },
    methods:{
      getTableData(){
        var that = this
        var params = {
          tableName: "Equipment",
          EquipmentCode:this.$route.query.EquipmentCode,
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res => {
          if(res.data.code === "200"){
            this.EquipmentData = res.data.data.rows[0]
          }else{
            that.$message({
              type: 'info',
              message: res.data.message
            });
          }
        })
      },
    }
  }
</script>

<style scoped>

</style>



