<template>
  <el-row>
    <el-col :span="24">
      <div class="platformContainer">
        <tableView class="" :tableData="TableData" @getTableData="getRoleTable" @privileges="privileges"></tableView>
      </div>
      <el-dialog :title="selectRoleName" :visible.sync="dialogVisible" width="50%">
        <el-transfer :titles="['', '']" :button-texts="['', '']" v-model="transferValue" :data="transferData"></el-transfer>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="savePrivileges"></el-button>
        </span>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
  import tableView from '@/components/CommonTable'
  export default {
    name: "Role",
    components:{tableView},
    data(){
      return {
        TableData:{
          tableName:"Role",
          column:[
            {label:"ID",prop:"ID",type:"input",value:"",disabled:true,showField:false,searchProp:false},
            {prop:"RoleName",label:"",type:"input",value:"",showField:true,searchProp:true},
            {prop:"RoleCode",label:"",type:"input",value:"",showField:true,searchProp:true},
            {prop:"Description",label:"",type:"input",value:"",showField:true,searchProp:false},
            {prop:"ParentNode",label:"",type:"input",value:"",showField:true,searchProp:true},
          ],
          data:[],
          limit:5,
          offset:1,
          total:0,
          multipleSelection:[],
          tableSelection:true, //
          searchProp:"",
          searchVal:"",
          handleType:[
            {type:"primary",label:"",clickEvent:"privileges",hasPermissions:['']},
          ],
        },
        dialogVisible:false,
        selectRoleName:"",
        transferValue:[],
        transferData:[],
      }
    },
    created(){
      this.getRoleTable()
    },
    methods:{
      getRoleTable(){
        var that = this
        var params = {
          tableName: this.TableData.tableName,
          limit:this.TableData.limit,
          offset:this.TableData.offset - 1
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res =>{
          if(res.data.code === "200"){
            var data = res.data.data
            that.TableData.data = data.rows
            that.TableData.total = data.total
          }
        },res =>{
          console.log("")
        })
      },
      privileges(){
        if(this.TableData.multipleSelection.length === 1){
          this.dialogVisible = true
          this.selectRoleName = ' '+this.TableData.multipleSelection[0].RoleName+' '
          this.transferData = []
          this.transferValue = []
          var that = this
          var params = {
            roleID:this.TableData.multipleSelection[0].ID
          }
          this.axios.get("/api/permission/selectpermissionbyrole",{
            params: params
          }).then(res =>{
            res.data.notHaveRows.forEach(item =>{
              that.transferData.push({
                key:item.ID,
                label:item.PermissionName
              })
            })
            res.data.existingRows.forEach(item =>{
              that.transferValue.push(item.ID)
            })
          },res =>{
            console.log("")
          })
        }else{
          this.$message({
            type: 'info',
            message: ''
          });
        }
      },
      savePrivileges(){
        var selectPermissionArr = []
        this.transferValue.forEach(item =>{
          selectPermissionArr.push(item)
        })
        var params = {
          roleID: this.TableData.multipleSelection[0].ID,
          permissionIDs:JSON.stringify(selectPermissionArr)
        }
        this.axios.post("/api/permission/saverolepermission",this.qs.stringify(params)).then(res =>{
          if(res.data.code === "200"){
            this.$message({
              type: 'success',
              message: ''
            });
            this.dialogVisible = false
          }
        },res =>{
          console.log("")
        })
      }
    }
  }
</script>

<style scoped>

</style>



