<template>
  <el-row :gutter="15">
    <el-col :span="24" v-if="$route.name != 'EquipmentDetails'">
      <el-form :inline="true">
        <el-row>
          <el-col :span="2">
            <el-form-item label=""></el-form-item>
          </el-col>
          <el-col :span="22">
            <el-form-item label="">
              <el-input v-model="TableData.searchField.EquipmentCode" placeholder="" size="mini" style="width: 150px;"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.searchField.EquipmentName" placeholder="" size="mini" style="width: 150px;"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.searchField.Grade" placeholder="" size="mini" style="width: 150px;"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.searchField.Status" placeholder="" size="mini" style="width: 150px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="mini" @click="getTableData"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="platformContainer">
        <el-form :inline="true">
          <el-form-item>
            <el-button type="primary" size="mini" @click="add"></el-button>
          </el-form-item>
        </el-form>
        <el-table :data="TableData.data" border size="mini" :header-cell-style="{ 'background':'#F5F7FA' }" ref="multipleTable" @select="handleSelect" @selection-change="handleSelectionChange" @row-click="handleRowClick">
          <el-table-column prop="EquipmentCode" label="">
            <template slot-scope="scope">
              <a href="javascript:;" style="color:#2196F3;" @click="seeDetails(scope.row.EquipmentCode)">{{ scope.row.EquipmentCode }}</a>
            </template>
          </el-table-column>
          <el-table-column prop="EquipmentName" label=""></el-table-column>
          <el-table-column prop="AssetCode" label=""></el-table-column>
          <el-table-column prop="LeaveFactoryCode" label=""></el-table-column>
          <!--<el-table-column prop="ElectronicCode" label=""></el-table-column>-->
          <el-table-column prop="EquipmentType" label=""></el-table-column>
          <!--<el-table-column prop="Brand" label=""></el-table-column>-->
          <el-table-column prop="Specs" label=""></el-table-column>
          <el-table-column prop="Unit" label=""></el-table-column>
          <el-table-column prop="Source" label=""></el-table-column>
          <el-table-column prop="Manufacturer" label=""></el-table-column>
          <el-table-column prop="Status" label="">
            <template slot-scope="scope">
              <span class="btn-block bg-success color-white" v-if="scope.row.Status === ''">{{ scope.row.Status }}</span>
              <span class="btn-block bg-orange color-white" v-if="scope.row.Status === ''">{{ scope.row.Status }}</span>
              <span class="btn-block bg-darkblue color-white" v-if="scope.row.Status === ''">{{ scope.row.Status }}</span>
              <span class="btn-block bg-red color-white" v-if="scope.row.Status === ''">{{ scope.row.Status }}</span>
              <span class="btn-block bg-grayblack color-white" v-if="scope.row.Status === ''">{{ scope.row.Status }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="Grade" label="">
            <template slot-scope="scope">
              <span class="btn-block color-white" style="background: #FF5722;" v-if="scope.row.Grade === 'A'">A()</span>
              <span class="btn-block color-white" style="background: #813EDB;" v-if="scope.row.Grade === 'B'">B()</span>
              <span class="btn-block color-white" style="background: #5FB878;"v-if="scope.row.Grade === 'C'">C()</span>
            </template>
          </el-table-column>
          <el-table-column prop="WarrantyTime" label=""></el-table-column>
          <el-table-column prop="UseTime" label=""></el-table-column>
          <el-table-column prop="IsCalculate" label=""></el-table-column>
          <!--<el-table-column prop="PurchaseTime" label=""></el-table-column>-->
          <!--<el-table-column prop="PurchaseMoney" label=""></el-table-column>-->
          <!--<el-table-column prop="ScrapTime" label=""></el-table-column>-->
          <!--<el-table-column prop="Director" label=""></el-table-column>-->
          <!--<el-table-column prop="Department" label=""></el-table-column>-->
          <!--<el-table-column prop="Position" label=""></el-table-column>-->
          <!--<el-table-column prop="IsDepreciation" label=""></el-table-column>-->
          <!--<el-table-column prop="NetValue" label=""></el-table-column>-->
          <!--<el-table-column prop="TechnicalParameter" label=""></el-table-column>-->
          <!--<el-table-column prop="Comment" label=""></el-table-column>-->
          <el-table-column label="" fixed="right" width="150">
            <template slot-scope="scope">
              <el-button size="mini" type="warning" @click="Edit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="Delete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="paginationClass">
          <el-pagination background  layout="total, sizes, prev, pager, next, jumper"
           :total="TableData.total"
           :current-page="TableData.offset"
           :page-sizes="[10,20,30,40,50]"
           :page-size="TableData.limit"
           @size-change="handleSizeChange"
           @current-change="handleCurrentChange">
          </el-pagination>
        </div>
        <el-dialog :title="TableData.dialogTitle" :visible.sync="TableData.dialogVisible" width="60%" :append-to-body="true">
          <el-form :inline="true" label-width="80px">
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.EquipmentCode" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.EquipmentName" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.AssetCode" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.LeaveFactoryCode" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.EquipmentType" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Brand" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Specs" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Unit" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Source" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Manufacturer" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="TableData.fieldModel.Status" size="small" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="TableData.fieldModel.Grade" size="small" placeholder="">
                <el-option label="A()" value="A"></el-option>
                <el-option label="B()" value="B"></el-option>
                <el-option label="C()" value="C"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.WarrantyTime" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.UseTime" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.IsCalculate" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.PurchaseTime" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.PurchaseMoney" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.ScrapTime" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Director" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Department" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Position" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.IsDepreciation" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.NetValue" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.TechnicalParameter" size="small"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="TableData.fieldModel.Comment" size="small"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="TableData.dialogVisible = false"> </el-button>
            <el-button type="primary" @click="save"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-col>
    <el-col :span="24" v-if="$route.name === 'EquipmentDetails'">
      <router-view :key="$route.fullPath"></router-view>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: "list",
    data(){
      return{
        TableData:{
          data:[],
          limit: 10,
          offset: 1,
          total: 0,
          multipleSelection: [],
          dialogTitle:"",
          dialogVisible:false,
          searchField:{
            EquipmentCode:"",
            EquipmentName:"",
            Grade:"",
            Status:"",
          },
          fieldModel:{
            ID:"",
            EquipmentCode:"",
            EquipmentName:"",
            AssetCode:"",
            LeaveFactoryCode:"",
            EquipmentType:"",
            Brand:"",
            Specs:"",
            Unit:"",
            Source:"",
            Manufacturer:"",
            Status:"",
            Grade:"",
            WarrantyTime:"",
            UseTime:"",
            IsCalculate:"",
            PurchaseTime:"",
            PurchaseMoney:"",
            ScrapTime:"",
            Director:"",
            Department:"",
            Position:"",
            IsDepreciation:"",
            NetValue:"",
            TechnicalParameter:"",
            Comment:"",
          },
        },
      }
    },
    mounted(){
      this.getTableData()
    },
    methods:{
      getTableData(){
        var that = this
        var params = {
          tableName: "Equipment",
          limit:this.TableData.limit,
          offset:this.TableData.offset - 1
        }
        for(var i in this.TableData.searchField){
          if(this.TableData.searchField[i]){
            params[i] = this.TableData.searchField[i]
          }
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res => {
          if(res.data.code === "200"){
            that.TableData.data = res.data.data.rows
            that.TableData.total = res.data.data.total
          }else{
            that.$message({
              type: 'info',
              message: res.data.message
            });
          }
        })
      },
      handleSizeChange(limit){ //
        this.TableData.limit = limit
        this.getTableData()
      },
      handleCurrentChange(offset) { // 
        this.TableData.offset = offset
        this.getTableData()
      },
      handleSelectionChange(row){
        this.TableData.multipleSelection = row
      },
      handleSelect(selection,row){  //
        this.$refs.multipleTable.clearSelection()
        this.$refs.multipleTable.toggleRowSelection(row)
        this.$refs.multipleTable.setCurrentRow(row)
      },
      handleRowClick(row){
        this.$refs.multipleTable.clearSelection()
        this.$refs.multipleTable.toggleRowSelection(row)
      },
      add(){
        this.TableData.dialogVisible = true
        this.TableData.dialogTitle = ""
      },
      Edit(index,row){
        this.TableData.dialogVisible = true
        this.TableData.dialogTitle = ""
        for(var i in this.TableData.fieldModel){
          this.TableData.fieldModel[i] = row[i]
        }
      },
      Delete(index,row){
        var mulId = [{id:row.ID}]
        var params = {tableName:"Equipment"}
        params.delete_data = JSON.stringify(mulId)
        this.$confirm(' '+ row.EquipmentName +' ', '', {
          distinguishCancelAndClose:true,
          type: 'warning'
        }).then(()  => {
          this.axios.delete("/api/CUID",{
            params:params
          }).then(res =>{
            if(res.data.code === "200"){
              this.$message({
                type: 'success',
                message: res.data.message
              });
            }
            this.getTableData()
          },res =>{
            console.log("")
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      save(){
        if(this.TableData.dialogTitle === ""){
          var params = {
            tableName:"Equipment",
          }
          for(var i in this.TableData.fieldModel){
            params[i] = this.TableData.fieldModel[i]
          }
          this.axios.post("/api/CUID",this.qs.stringify(params)).then(res =>{
            if(res.data.code === "200"){
              this.$message({
                type: 'success',
                message: res.data.message
              });
              this.TableData.dialogVisible = false
              this.getTableData()
              this.CreateQrcode()
            }else{
              this.$message({
                type: 'info',
                message: res.data.message
              });
            }
          },res =>{
            console.log("")
          })
        }else if(this.TableData.dialogTitle === ""){
          var params = {
            tableName:"Equipment",
          }
          for(var i in this.TableData.fieldModel){
            params[i] = this.TableData.fieldModel[i]
          }
          this.axios.put("/api/CUID",this.qs.stringify(params)).then(res =>{
            if(res.data.code === "200"){
              this.$message({
                type: 'success',
                message: res.data.message
              });
              this.TableData.dialogVisible = false
              this.getTableData()
            }else{
              this.$message({
                type: 'info',
                message: res.data.message
              });
            }
          },res =>{
            console.log("")
          })
        }
      },
      CreateQrcode(){
        var params = {
          EquipmentCode:this.TableData.fieldModel.EquipmentCode,
        }
        this.axios.post("/api/qrcode",this.qs.stringify(params)).then(res =>{
          if(res.data.code === "1000"){
            this.$message({
              type: 'success',
              message: res.data.msg
            });
          }else{
            this.$message({
              type: 'info',
              message: res.data.message
            });
          }
        },res =>{
          console.log("")
        })
      },
      seeDetails(EquipmentCode){
        this.$store.commit('setEquipmentCode',EquipmentCode)
        this.$router.push("/EquipmentDetails")
      }
    }
  }
</script>

<style scoped>

</style>



