<template>
  <el-container class="body-container">
    <!--  -->
    <el-header class="body-head" :style="{ background: themeColor }">
      <div class="head-menu floatLeft">
        <router-link to='/home' style="display: block;height: 100%;">
          <el-image style="width: 80px;height: calc(100% - 18px);margin-top: 10px;transition: all 0.3s ease-out;" :src="require('../assets/logo.png')"></el-image>
        </router-link>
      </div>
      <div class="head-menu floatLeft marginLeft">
        <router-link to='/home'>
          <span class="head-title text-size-20"></span>
        </router-link>
      </div>
      <div class="head-menu floatRight">
        <ul>
          <li>
            <el-tooltip class="head-menu-item" effect="dark" content="" placement="bottom">
              <i :class="isFullScreen?'el-icon-aim':'el-icon-full-screen'" @click="getFullCreeen"></i>
            </el-tooltip>
          </li>
          <li>
            <el-tooltip class="head-menu-item" effect="light" placement="bottom">
              <div slot="content">
                <el-color-picker v-model="themeColor" @change="changeTheme"></el-color-picker>
              </div>
              <i class="el-icon-brush"></i>
            </el-tooltip>
          </li>
          <li>
            <el-dropdown class="head-menu-item" trigger="click" @command="handleCommand">
              <span class="el-dropdown-link text-size-16">
                <i class="dotState bg-darkblue"></i>{{ this.$store.state.UserName }}<i class="el-icon-arrow-down el-icon--right text-size-12"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a"></el-dropdown-item>
                <el-dropdown-item command="b" style="text-align: center"><i class="fa fa-power-off"></i></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dialog title="" :visible.sync="dialogUserVisible" :append-to-body="true" width="50%">
              <el-form>
                <el-form-item label="">{{ userInfo.Name }}</el-form-item>
                <el-form-item label="">{{ userInfo.WorkNumber }}</el-form-item>
                <el-form-item label="">{{ userInfo.LastLoginTime }}</el-form-item>
                <el-form-item label="">{{ userInfo.Permissions }}</el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogUserVisible = false"> </el-button>
              </span>
            </el-dialog>
          </li>
        </ul>
      </div>
    </el-header>
    <el-container>
      <!--  -->
      <el-aside width="220px" class="left-aside">
        <el-row>
          <el-col :span="24">
            <div :style="selfHeight" class="aside-menu">
              <el-menu class="menu-ul" :default-active="defaultActiveUrl" :collapse="menuIsCollapse" :router="true" @select="menuSelect" :active-text-color="themeColor">
                <template v-for="item in systemOptions" :index="item.url">
                  <el-menu-item v-if="!item.mainMenu" :index="item.url"><i :class="item.icon"></i><span slot="title">{{ item.title }}</span></el-menu-item>
                  <el-submenu v-if="item.mainMenu" :index="item.title">
                    <template slot="title"><i :class="item.icon"></i><span>{{ item.title }}</span></template>
                    <el-menu-item v-for="(child,childIndex) in item.mainMenu" :key="childIndex" :index="child.url"><span style="margin-left:10px;">{{child.title}}</span></el-menu-item>
                  </el-submenu>
                </template>
              </el-menu>
            </div>
            <div class="aside-foot">
              <el-button :icon="sideIcon" size="mini" circle @click="iconToggle" :style="{ background: themeColor }"></el-button>
            </div>
          </el-col>
        </el-row>
      </el-aside>
      <!--  -->
      <el-container>
        <el-header style="height: 40px;padding-top:10px;">
          <el-breadcrumb separator="/" style="line-height: 40px;">
            <el-breadcrumb-item v-for="(item,index) in breadList" :key="index" :to="{ path: item.path }">{{ item.meta.title }}</el-breadcrumb-item>
          </el-breadcrumb>
        </el-header>
        <el-main style="clear: both;">
          <transition name="move" mode="out-in">
           <!---->
            <router-view :key="$route.fullPath"></router-view>
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
  var moment = require('moment');
  import screenfull from "screenfull"
  export default {
    name: 'Index',
    data () {
      return {
        selfHeight:{ //
          height:''
        },
        menuIsCollapse: false, //
        sideIcon:'el-icon-arrow-left', //
        systemOptions:[
          {title: '',icon:"el-icon-document",mainMenu:[
              {title:"",url:"/list"},
              {title:"",url:"/equipmentTree"},
              {title:"",url:"/Inventory"},
              {title:"",url:"/depreciation"},
              {title:"",url:"/labelPrinting"},
              {title:"",url:"/runRecord"},
            ]},
          {title: '',icon:"fa fa-exchange",mainMenu:[
              {title:"",url:"/transfer"},
              {title:"",url:"/scrap"},
              {title:"",url:"/sellOff"},
              {title:"",url:"/receive"},
            ]},
          {title: '',icon:"fa fa-wrench",mainMenu:[
              {title:"",url:"/maintainStandard"},
              {title:"",url:"/repair"},
              {title:"",url:"/Outsourced"},
              {title:"",url:"/servicedPlan"},
              {title:"",url:"/servicedTask"},
            ]},
          {title: '',icon:"fa fa-history",mainMenu:[
              {title:"",url:"/upkeepStandard"},
              {title:"",url:"/upkeepPlan"},
              {title:"",url:"/upkeepTask"},
            ]},
          {title: '',icon:"fa fa-compass",mainMenu:[
              {title:"",url:"/inspectionStandard"},
              {title:"",url:"/pointInspection"},
              {title:"",url:"/pointInspectionLog"},
              {title:"",url:"/patrolPlan"},
              {title:"",url:"/patrolTask"},
            ]},
          {title: '',icon:"fa fa-archive",mainMenu:[
              {title:"",url:"/partsList"},
              {title:"",url:"/partsReceive"},
              {title:"",url:"/warehouseOut"},
              {title:"",url:"/warehouseEnter"},
              {title:"",url:"/stockTaking"},
            ]},
          {title: '',icon:"el-icon-view",mainMenu:[
              {title:"",url:"/realTimeData"},
              {title:"",url:"/dataReport"},
            ]},
          {title: '',icon:"fa fa-bar-chart",mainMenu:[
              {title:"",url:"/EquipmentStatistics"},
              {title:"",url:"/upkeepStatistics"},
              {title:"",url:"/maintainStatistics"},
              {title:"",url:"/examineStatistics"},
              {title:"",url:"/warehouseStatistics"},
              {title:"",url:"/runingStatistics"},
            ]},
          {title: '',icon:"el-icon-s-tools",mainMenu:[
              {title:"",url:"/Organization"},
              {title:"",url:"/FactoryManagement"},
              {title:"",url:"/Role"},
              {title:"",url:"/TeamGroup"},
              {title:"",url:"/Personnel"},
              {title:"",url:"/Permission"},
              {title:"",url:"/flowGraph"},
              {title:"",url:"/BuildTable"},
              {title:"",url:"/Log"}
            ]},
        ],
        defaultActiveUrl:"",
        dialogUserVisible:false, //
        userInfo:{},
        isFullScreen:false, //
        themeColor:"",
        breadList:[],
      }
    },
    mounted(){
      if(localStorage.getItem('theme')){
        this.themeColor = localStorage.getItem('theme')
      }else{
        this.themeColor = "#4881e6"
      }
    },
    created(){
      document.title = ""
      window.addEventListener('resize', this.getMenuHeight);
      this.getMenuHeight()
      if(sessionStorage.getItem("LoginStatus")) {
        this.$store.commit('setUser',sessionStorage.getItem('WorkNumber'))
      }else{
        this.$router.push("/login");
      }
      this.getBreadcrumb()
      if(this.$route.path === "/home"){ // 
        this.defaultActiveUrl = ""
      }else{
        this.systemOptions.forEach((menu,i) =>{
          if(menu.title === this.$route.meta.type){
            this.defaultActiveUrl = this.$route.path
          }
        })
      }
    },
    destroyed() {

    },
    watch:{
      $route:{
        handler(val,oldval){
          this.defaultActiveUrl = val.path
          this.getBreadcrumb();
        },
        deep: true,
      }
    },
    methods:{
      getMenuHeight(){
        if(this.menuIsCollapse){
          this.selfHeight.height = window.innerHeight - 180+'px';
        }else{
          this.selfHeight.height = window.innerHeight - 180+'px';
        }
      },
      menuSelect(url,title){  //  query
        this.$router.push({
          query:moment()
        })
      },
      handleCommand(command) {  //
        if(command === "a"){
          this.dialogUserVisible = true
          this.userInfo.LastLoginTime = sessionStorage.getItem('LastLoginTime')
          this.userInfo.WorkNumber = sessionStorage.getItem('WorkNumber')
          this.userInfo.Name = sessionStorage.getItem('UserName')
          this.userInfo.Permissions = JSON.parse(sessionStorage.getItem('Permissions')).join('')
        }else if(command === "b"){
          this.$store.commit('removeUser')
          this.$router.replace("/login")
        }
      },
      iconToggle() {  //
        this.menuIsCollapse = !this.menuIsCollapse
        this.getMenuHeight()
        if(this.menuIsCollapse){
          this.sideIcon = 'el-icon-arrow-right'
          $(".left-aside").animate({"width":"64px"})
        }else{
          this.sideIcon = 'el-icon-arrow-left'
          $(".left-aside").animate({"width":"220px"})
        }
      },
      getFullCreeen () {  //
        if (screenfull.isEnabled) {
          screenfull.toggle()
          if(screenfull.isFullscreen){
            this.isFullScreen = false
          }else{
            this.isFullScreen = true
          }
        }
      },
      changeTheme(value){
        this.themeColor = value
        localStorage.setItem('theme', value);
      },
      getBreadcrumb(){
        if(this.$route.name != "home") {
          this.breadList = [{ path: "/home", meta: { title: "" } }].concat(this.$route.matched);
        }else{
          this.breadList = [{ path: "/home",name:"home", meta: { title: "" } }]
        }
      },
    }
  }
</script>
<style lang="less" scoped>

</style>



