<template>
  <el-row>
    <el-col :span="24">
      <div class="platformContainer">
        <tableView class="" :tableData="TableData" @getTableData="getRoleTable"></tableView>
      </div>
    </el-col>
  </el-row>
</template>

<script>
  import tableView from '@/components/CommonTable'
  export default {
    name: "TeamGroup",
    components:{tableView},
    data(){
      return {
        TableData:{
          tableName:"ShiftsGroup",
          column:[
            {prop:"ID",label:"ID",type:"input",value:"",disabled:true,showField:false,searchProp:false},
            {prop:"ShiftsGroupCode",label:"",type:"input",value:""},
            {prop:"ShiftsGroupName",label:"",type:"input",value:""},
            {prop:"ShiftsGroupType",label:"",type:"input",value:""},
            {prop:"Description",label:"",type:"input",value:"",showField:false,searchProp:false},
          ],
          data:[],
          limit:5,
          offset:1,
          total:0,
          searchProp:"",
          tableSelection:true, //
          tableSelectionRadio:false, //
          searchVal:"",
          multipleSelection: [],
          dialogVisible: false,
          dialogTitle:'',
          handleType:[
            {type:"primary",label:"",hasPermissions:['']},
            {type:"warning",label:"",hasPermissions:['']},
            {type:"danger",label:"",hasPermissions:['']},
          ],
        },
      }
    },
    created(){
      this.getRoleTable()
    },
    methods:{
      getRoleTable(){
        var that = this
        var params = {
          tableName: this.TableData.tableName,
          limit:this.TableData.limit,
          offset:this.TableData.offset - 1
        }
        this.axios.get("/api/CUID",{
          params: params
        }).then(res =>{
          if(res.data.code === "200"){
            var data = res.data.data
            that.TableData.data = data.rows
            that.TableData.total = data.total
          }
        },res =>{
          console.log("")
        })
      }
    }
  }
</script>

<style scoped>

</style>



