import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import home from '@/views/home'
import Login from '@/components/Login'
import Organization from '@/views/system/Organization'
import FactoryManagement from '@/views/system/FactoryManagement'
import Role from '@/views/system/Role'
import TeamGroup from '@/views/system/TeamGroup'
import Personnel from '@/views/system/Personnel'
import Permission from '@/views/system/Permission'
import Log from '@/views/system/Log'
import flowGraph from '@/views/system/flowGraph'
import BuildTable from '@/views/system/BuildTable'
//
import mobileEquipmentDetails from '@/views/mobile/mobileEquipmentDetails'

// 
import list from '@/views/Equipment/list'
import EquipmentDetails from '@/views/Equipment/EquipmentDetails'
import equipmentTree from '@/views/Equipment/equipmentTree'
import Inventory from '@/views/Equipment/Inventory'
import depreciation from '@/views/Equipment/depreciation'
import labelPrinting from '@/views/Equipment/labelPrinting'
import runRecord from '@/views/Equipment/runRecord'

//
import transfer from '@/views/allocation/transfer'
import scrap from '@/views/allocation/scrap'
import sellOff from '@/views/allocation/sellOff'
import receive from '@/views/allocation/receive'

//
import maintainStandard from '@/views/maintain/maintainStandard'
import repair from '@/views/maintain/repair'
import Outsourced from '@/views/maintain/Outsourced'
import servicedPlan from '@/views/maintain/servicedPlan'
import servicedTask from '@/views/maintain/servicedTask'

//
import upkeepStandard from '@/views/upkeep/upkeepStandard'
import upkeepPlan from '@/views/upkeep/upkeepPlan'
import upkeepTask from '@/views/upkeep/upkeepTask'


//
import inspectionStandard from '@/views/inspection/inspectionStandard'
import pointInspection from '@/views/inspection/pointInspection'
import pointInspectionLog from '@/views/inspection/pointInspectionLog'
import patrolPlan from '@/views/inspection/patrolPlan'
import patrolTask from '@/views/inspection/patrolTask'

//
import partsList from '@/views/spareParts/partsList'
import partsReceive from '@/views/spareParts/partsReceive'
import warehouseOut from '@/views/spareParts/warehouseOut'
import warehouseEnter from '@/views/spareParts/warehouseEnter'
import stockTaking from '@/views/spareParts/stockTaking'

//
import realTimeData from '@/views/monitoring/realTimeData'
import dataReport from '@/views/monitoring/dataReport'

//
import EquipmentStatistics from '@/views/statistics/EquipmentStatistics'
import upkeepStatistics from '@/views/statistics/upkeepStatistics'
import maintainStatistics from '@/views/statistics/maintainStatistics'
import examineStatistics from '@/views/statistics/examineStatistics'
import warehouseStatistics from '@/views/statistics/warehouseStatistics'
import runingStatistics from '@/views/statistics/runingStatistics'

Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home', //indexhome
      children:[
        {path:'/home',name:'home',meta:{ title:''},component:home},
        {path:'/Organization',name:'Organization',meta:{ title:'',type:""},component:Organization},
        {path:'/FactoryManagement',name:'FactoryManagement',meta:{ title:'',type:""},component:FactoryManagement},
        {path:'/Role',name:'Role',meta:{ title:'',type:""},component:Role},
        {path:'/TeamGroup',name:'TeamGroup',meta:{ title:'',type:""},component:TeamGroup},
        {path:'/Personnel',name:'Personnel',meta:{ title:'',type:""},component:Personnel},
        {path:'/Permission',name:'Permission',meta:{ title:'',type:""},component:Permission},
        {path:'/Log',name:'Log',meta:{ title:'',type:""},component:Log},
        {path:'/flowGraph',name:'flowGraph',meta:{ title:'',type:""},component:flowGraph},
        {path:'/BuildTable',name:'BuildTable',meta:{ title:'',type:""},component:BuildTable},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home', //indexhome
      children:[
        {path:'/list',name:'list',meta:{ title:'',type:""},component:list,children:[
            {path:'/EquipmentDetails',name:'EquipmentDetails',meta:{ title:''},component:EquipmentDetails},
          ]},
        {path:'/equipmentTree',name:'equipmentTree',meta:{ title:'',type:""},component:equipmentTree},
        {path:'/Inventory',name:'Inventory',meta:{ title:'',type:""},component:Inventory},
        {path:'/depreciation',name:'depreciation',meta:{ title:'',type:""},component:depreciation},
        {path:'/labelPrinting',name:'labelPrinting',meta:{ title:'',type:""},component:labelPrinting},
        {path:'/runRecord',name:'runRecord',meta:{ title:'',type:""},component:runRecord},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/transfer',name:'transfer',meta:{ title:'',type:""},component:transfer},
        {path:'/scrap',name:'scrap',meta:{ title:'',type:""},component:scrap},
        {path:'/sellOff',name:'sellOff',meta:{ title:'',type:""},component:sellOff},
        {path:'/receive',name:'receive',meta:{ title:'',type:""},component:receive},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/maintainStandard',name:'maintainStandard',meta:{ title:'',type:""},component:maintainStandard},
        {path:'/repair',name:'repair',meta:{ title:'',type:""},component:repair},
        {path:'/Outsourced',name:'Outsourced',meta:{ title:'',type:""},component:Outsourced},
        {path:'/servicedPlan',name:'servicedPlan',meta:{ title:'',type:""},component:servicedPlan},
        {path:'/servicedTask',name:'servicedTask',meta:{ title:'',type:""},component:servicedTask},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/upkeepStandard',name:'upkeepStandard',meta:{ title:'',type:""},component:upkeepStandard},
        {path:'/upkeepPlan',name:'upkeepPlan',meta:{ title:'',type:""},component:upkeepPlan},
        {path:'/upkeepTask',name:'upkeepTask',meta:{ title:'',type:""},component:upkeepTask},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/inspectionStandard',name:'inspectionStandard',meta:{ title:'',type:""},component:inspectionStandard},
        {path:'/pointInspection',name:'pointInspection',meta:{ title:'',type:""},component:pointInspection},
        {path:'/pointInspectionLog',name:'pointInspectionLog',meta:{ title:'',type:""},component:pointInspectionLog},
        {path:'/patrolPlan',name:'patrolPlan',meta:{ title:'',type:""},component:patrolPlan},
        {path:'/patrolTask',name:'patrolTask',meta:{ title:'',type:""},component:patrolTask},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/partsList',name:'partsList',meta:{ title:'',type:""},component:partsList},
        {path:'/partsReceive',name:'partsReceive',meta:{ title:'',type:""},component:partsReceive},
        {path:'/warehouseOut',name:'warehouseOut',meta:{ title:'',type:""},component:warehouseOut},
        {path:'/warehouseEnter',name:'warehouseEnter',meta:{ title:'',type:""},component:warehouseEnter},
        {path:'/stockTaking',name:'stockTaking',meta:{ title:'',type:""},component:stockTaking},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/realTimeData',name:'realTimeData',meta:{ title:'',type:""},component:realTimeData},
        {path:'/dataReport',name:'dataReport',meta:{ title:'',type:""},component:dataReport},
      ]
    },
    {
      path: '/',
      name: 'Index',
      meta:{ title:'' },
      component: Index,
      redirect:'/home',
      children:[
        {path:'/EquipmentStatistics',name:'EquipmentStatistics',meta:{ title:'',type:""},component:EquipmentStatistics},
        {path:'/upkeepStatistics',name:'upkeepStatistics',meta:{ title:'',type:""},component:upkeepStatistics},
        {path:'/maintainStatistics',name:'maintainStatistics',meta:{ title:'',type:""},component:maintainStatistics},
        {path:'/examineStatistics',name:'examineStatistics',meta:{ title:'',type:""},component:examineStatistics},
        {path:'/warehouseStatistics',name:'warehouseStatistics',meta:{ title:'',type:""},component:warehouseStatistics},
        {path:'/runingStatistics',name:'runingStatistics',meta:{ title:'',type:""},component:runingStatistics},
      ]
    },
    {
      path: '/mobileEquipmentDetails',
      name: 'mobileEquipmentDetails',
      component: mobileEquipmentDetails,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
  ]
})



